<template>
  <div>
    <v-menu offset-y>
      <template v-slot:activator="{ on }">
        <div v-on="on">
          <v-tooltip bottom>
        <template v-slot:activator="{ on }">
        <v-btn color="transparent" v-on="on" icon>
          <img v-if="language" :src="require('@/assets/images/lang/'+language+'.png')" alt>
        </v-btn>
      </template>
      <span>{{ $t("tooltip.LANG_SWITCH") }}</span>
      </v-tooltip>
        </div>
      </template>
      <v-list>
        <v-list-tile
          v-for="(lang, index) in langList"
          :key="index"
          :disabled="language === lang.code"
        >
          <v-list-tile-avatar>
            <v-avatar size="32px" tile @click="setLanguage(lang.code)">
              <img :src="require('@/assets/images/lang/'+lang.code+'.png')" alt="language">
            </v-avatar>
          </v-list-tile-avatar>
          <v-list-tile-title @click="setLanguage(lang.code)">{{ lang.name }}</v-list-tile-title>
        </v-list-tile>
      </v-list>
    </v-menu>
  </div>
</template>


<script>
export default {
  data: () => ({
    language: "",
    langList: [
      {
        code: "zh_CN",
        name: ""
      },
      {
        code: "en_US",
        name: "English"
      }
    ]
  }),
  methods: {
    // 
    setLanguage(lang) {
      this.language = lang;
      // 
      this.$i18n.locale = lang;
      // 
      this.$validator.locale = lang;

      // storesetLanguage
      this.$store.dispatch("setLanguage", lang);
    }
  },
  mounted() {
    // vuex
    this.language = this.$store.state.language;
  }
};
</script>