<template>
  <v-tooltip bottom>
    <template v-slot:activator="{ on }">
      <v-btn icon v-on="on" @click="handleFullScreen">
        <v-icon>{{ isFullScreen?'fullscreen_exit':'fullscreen'}}</v-icon>
      </v-btn>
    </template>
    <span>{{ isFullScreen?$t("tooltip.FULLSCREEN_EXIT"):$t("tooltip.FULLSCREEN_ENTER") }}</span>
  </v-tooltip>
</template>


<script>
import Util from "@/util";
import { mapState } from "vuex";
export default {
  data: () => ({
    isFullScreen: false
  }),
  methods: {
    handleFullScreen() {
      Util.toggleFullScreen();
      this.isFullScreen = !this.isFullScreen;
      this.$store.dispatch("setFullScreen", this.isFullScreen);
    },
    /**
     * ESC
     */
    checkFullScreen() {
      // document.fullscreenEnabled
      var isFullScreen =
        window.fullScreen ||
        document.webkitIsFullScreen ||
        document.msFullscreenEnabled;
      // to fix : false || undefined == undefined
      if (isFullScreen === undefined) {
        isFullScreen = false;
      }
      return isFullScreen;
    }
  },
  computed: {},
  mounted() {
    window.onresize = () => {
      // ESC
      if (!this.checkFullScreen()) {
        // esc
        this.isFullScreen = false;
      }
      this.$store.dispatch("setFullScreen", this.isFullScreen);
    };
  }
};
</script>